@tailwind base;
@tailwind components;
@tailwind utilities;

:root {
    --bg-color: #171717;
    --main-color: #facc15;
    --sub-color: #cbd5e1;
    --sub-accent-color: #64748b;
    --text-color: #52525b;
    --mistake-color: #e11d48;
    --text-correct-color: #e4e4e7;
}

*,
*::after,
*::before {
    box-sizing: border-box;
}

body {
    user-select: none;
    @apply font-mono
}

.page-content {
    background-color: var(--bg-color);
}

.header {
    color: var(--sub-color)
}

.timer, .words-countdown {
    color: var(--main-color)
}

.words-container {
    overflow: visible clip;
    transition: all 0.15s ease;
}

.blur-warning {
    color: var(--sub-color)
}

.base {
    color: var(--text-color);
    @apply text-3xl font-semibold
}

.correct {
    color: var(--text-correct-color);
    @apply text-3xl font-semibold
}

.incorrect {
    color: var(--mistake-color);
    @apply text-3xl font-semibold
}
.mode {
    background-color: var(--sub-accent-color);
    color: var(--sub-color)
}

.mode-btn {
    color: var(--sub-color)
}

.mode-active {
    color: var(--main-color)
}

.result-left {
    p {
        color: var(--text-color);

        span {
            color: var(--main-color);
        }
    }

}

.restart-btn::after {
    opacity: 0;
    visibility: hidden;
    content: "Restart";
    position: absolute;
    color: #FFF;
    background-color: #101112;
    padding: 5px 20px;
    left: 50%;
    bottom: 0;
    transform: translate(-50%, 120%);
    border-radius: 5px;
    transition: opacity 0.3s ease;
}

.restart-btn::before {
    content: "";
    opacity: 0;
    visibility: hidden;
    position: absolute;
    clip-path: polygon(50% 0%, 0% 100%, 100% 100%);
    width: 10px;
    height: 10px;
    background-color: #101112;
    bottom: 0;
    left: 50%;
    transform: translate(-50%, 120%);
    transition: opacity 0.3s ease;
}

.restart-btn:hover::after,
.restart-btn:hover::before {
    opacity: 1;
    visibility: visible;
}

.restart-btn:focus::after,
.restart-btn:focus::before  {
    opacity: 1;
    visibility: visible;
}

.result-footer {
    p {
        color: var(--text-color);

        span {
            color: var(--main-color);
        }
    }

}

.chart-line {
    stroke: var(--main-color);
}

.commands-tips {
    color: var(--sub-accent-color);
    font-size: 12px;
}

.commands-key {
    background-color: var(--sub-color);
    padding: 2px 5px;
}

.themes-sidebar {
    margin-top: -205px;
    margin-left: 10px;
    border: 1px solid var(--sub-color);
    padding: 50px 0px;
    bottom: 50%;
    transform: translateY(50%);
}

.themes-sidebar-title {
    color: var(--main-color);
}


.caret {
    content: "";
    position: absolute;
    width: 2px;
    height: 2rem;
    top: 5%;
    left: -1px;
    opacity: 0.6;
    transition: transform 0.2s ease-out;
    animation: caret-animation 2s infinite;
    background-color: var(--main-color);
  }


@keyframes caret-animation {
    0% {
        opacity: 0;
    }
    
    50% {
        opacity: 1;
    }

    100% {
        opacity: 0;
    }
}